// BASIC
import React, { Component } from 'react'
import styled from 'styled-components'
import {Link} from 'react-router-dom'
// ICONS
import { FaUserTie } from 'react-icons/fa'
import { au } from '../../../Config/firebase'


const SocialMediaElement = styled.ul`
	/* display: none; */
	position: absolute;
	bottom: 0;
	right: 20vw;
	display: flex;
	justify-content: center;
	list-style: none;
	width: 60vw;
	padding: 0;
	text-align: center;
	font-size: 30px;
	/* opacity: 0.5; */
	transition-duration: 0.5s;
	animation: floatBottom 0.3s ease-in;
	z-index: 2;
	@media(max-height: 550px) {
		display: none;
	}
`

// const SocialMediaItem = styled.li`
// 	margin: 0 10px;
// `

const SocialMediaItem = styled(Link)`
	text-decoration: none;
	color: var(--color-decorative);
	margin: 0 10px;
	transition: all 0.2s ease;
	:hover {
		opacity: 0.7;
	}
`

class SocialMedia extends Component {
	state = {
		userId: null
	}
	componentWillMount() {
		au.onAuthStateChanged(user => {
			if (user) {
				this.setState({userId: user.uid})
			}
		})
	}
	render() {
		return (
			<SocialMediaElement>
				{/* <SocialMediaItem>
					<BrowserLink href="https://www.facebook.com/programista.webowy.jakub.schulz" target="_blank">
						<FaFacebookF />
					</BrowserLink>
				</SocialMediaItem>
				<SocialMediaItem>
					<BrowserLink href="https://github.com/jakub104" target="_blank">
						<FaGithub />
					</BrowserLink>
				</SocialMediaItem>
				<SocialMediaItem>
					<BrowserLink href="https://www.linkedin.com/in/jakub-schulz-265931186/" target="_blank">
						<FaLinkedinIn />
					</BrowserLink>
				</SocialMediaItem>
				<SocialMediaItem>
					<BrowserLink href="https://programista-webowy-jakub-schulz.000webhostapp.com" target="_blank">
						<FaUserTie />
					</BrowserLink>
				</SocialMediaItem> */}
				{!this.state.userId ? '' : <SocialMediaItem to={`/users/${this.state.userId}`}>
					<FaUserTie />
				</SocialMediaItem>}
			</SocialMediaElement>
		);
	}
}

export default SocialMedia